

import React, { Component, useState } from 'react';
import { connect } from 'react-redux'
import Form from '../../../../components/form/form';
import InputType from '../../../../components/form/form-controls/input';
import Button from '../../../../components/form/button/button';
import ErrorBox from '../../../../components/form/error-box/error-box';
import Loader from '../../../../components/ui/loader/loader';
import Dropdown from '../../../../components/form/form-controls/select'
import VersionDetailModal from '../../../../components/layout/modal/modal'
import RichTextEditor from 'react-rte'
import ReactTextEditor from '../../../../components/form/rich-text-editor/rich-text-editor'
import Card from '../../../../components/ui/card/card'
import Divider from '../../../../components/ui/divider/divider'
import { Select, Modal } from 'antd';
//
import { GetToken } from '../../../../helpers/token';
import { createVersion } from '../../../../services/api/product_catalog';
import * as actionType from '../../../../store/actions/action-type'
//
import './create_version.css'

const Option = Select.Option;

const CreateVersion = (props) => {

    const [gToken, setgToken] = useState(GetToken())
    const [version, setversion] = useState('')
    const [versionDetail, setversionDetail] = useState(RichTextEditor.createEmptyValue())
    const [productId, setproductId] = useState('')
    const [detailsId, setdetailsId] = useState('')
    const [productIdWarn, setproductIdWarn] = useState(false)
    const [versionWarn, setversionWarn] = useState(false)
    const [errorMsg, seterrorMsg] = useState(false)
    const [showLoader, setshowLoader] = useState(false)
    const [visible, setvisible] = useState(false)

    const success = () => {
        Modal.success({
            title: 'Successful',
            content: 'Product version has been created.',
        });
    };

    const callback = (data) => {
        if (data.status === 200) {
            props.versionDispatch(data.data);
            success();
            reset();
        } else {
            seterrorMsg('Something went wrong. Please try again.')
            setshowLoader(false)
        }
    }

    const onFormSubmit = (e) => {
        seterrorMsg('')
        setshowLoader(true)
        e.preventDefault();

        const data = {
            version: version,
            versionDetail: versionDetail.toString('html')
        }
        if (validate() && gToken) {
            outlineColor();
            createVersion(callback, productId, data, gToken)
        } else {
            outlineColor();
            seterrorMsg('Please fill all the required fields.')
            setshowLoader(false)
        }
    }

    const onInputChange = (e) => {
        e.target.name(e.target.value);
    };

    const textChange = (value) => {
        setversionDetail(value);
    };

    const onProductSelect = (value) => {
        setproductId(value)
    }

    const outlineColor = () => {
        if (!productId.trim()) {
            setproductIdWarn(true)
        } else {
            setproductIdWarn(false)
        } if (!version.trim()) {
            setversionWarn(true)
        } else {
            setversionWarn(false)
        }
    }

    const validate = () => {
        return version.trim() !== '' && productId.trim() !== '';
    }

    const reset = () => {
        setproductId('')
        setversion('')
        setversionDetail(RichTextEditor.createEmptyValue())
        setshowLoader(false)
        seterrorMsg('')
    }

    const openModal = (pId, vId) => {
        setvisible(true)
        setproductId(pId)
        setdetailsId(vId)
    }

    const cancelClick = () => {
        setvisible(false)
        setproductId('')
        setdetailsId('')
    }

    let filterProduct = props.products.filter((product) => product._id === productId);
    let filterDetail = filterProduct.length > 0 ? filterProduct[0].details.filter((detail) => detail._id === detailsId) : '';

    return (
        <div className="container flex-row">
            <VersionDetailModal
                heading="Version Details"
                showModal={visible}
                cancelClick={cancelClick}
                footer={null}
                maskClosable={false}
            >
                {filterDetail.length > 0 ? <div dangerouslySetInnerHTML={{ __html: filterDetail[0].versionDetail }} /> : 'No data'}
            </VersionDetailModal>
            <div className="container-left">
                <div className="product-form">
                    <Form onSubmitHandler={onFormSubmit}>
                        <Dropdown
                            labelName="Product"
                            placeholder="Select Product"
                            optionFilterProp="children"
                            onSelect={onProductSelect}
                            size="large"
                            isRequired={true}
                            filterOption={(input, option) => option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0}
                            classValue={productIdWarn ? 'inputField-outline' : null}
                        >
                            {
                                props.products.map((product) => (
                                    <Option value={product._id} key={product._id}>{product.name}</Option>
                                ))
                            }
                        </Dropdown>
                        <ErrorBox errorMsgs={<strong>If product is not listed above then&nbsp;<b>Create Product.</b></strong>}></ErrorBox>
                        <InputType
                            type="text"
                            value={version}
                            name="version"
                            labelName="Product Version"
                            placeholder="Enter Product Version"
                            onInputChange={onInputChange}
                            isRequired={true}
                        />
                        <ReactTextEditor
                            labelName="Product Version Details"
                            value={versionDetail}
                            textChange={textChange}
                            placeholder="Enter Version Details"
                        />
                        <ErrorBox errorMsgs={<strong>If you are creating new version of product then&nbsp;<b>Write version details.</b></strong>}></ErrorBox>
                        {errorMsg ? <ErrorBox errorMsgs={errorMsg} /> : null}
                        {showLoader ? <Loader>Creating...</Loader> : null}
                        <div className="issue-form-button">
                            <Button isType='primary' htmlTypes='submit'>Create</Button>
                        </div>
                    </Form>
                </div>
            </div>
            <div className="container-right">
                {
                    props.products.map((product) => (
                        (
                            <div key={product._id} className="versionTable">
                                <Card>
                                    <h3><b>{product.name} Versions</b></h3>
                                    <Divider />
                                    {
                                        product.details.length > 0
                                            ?
                                            <div className="versionList">
                                                {
                                                    product.details.map((version) => (
                                                        <a onClick={() => openModal(product._id, version._id)} className="versionListCol" key={version._id}>
                                                            {version.version}
                                                        </a>
                                                    ))
                                                }
                                            </div>
                                            : <div className="u-text-center">No data</div>
                                    }

                                </Card>
                            </div>
                        )
                    ))
                }
            </div>
        </div>
    )

}

function mapDispatchToProps(dispatch) {
    return {
        versionDispatch: (data) => {
            dispatch({
                type: actionType.ADD_VERSION,
                value: data
            })
        }
    }
}

export default connect(null, mapDispatchToProps)(CreateVersion)