 

import React, { Component , useState } from 'react';
import {connect} from 'react-redux';
import Form from '../../../../components/form/form';
import InputType from '../../../../components/form/form-controls/input';
import Button from '../../../../components/form/button/button';
import ErrorBox from '../../../../components/form/error-box/error-box';
import Loader from '../../../../components/ui/loader/loader'
import { Table, Modal } from 'antd';
//
import {createProduct} from '../../../../services/api/product_catalog'
import {GetToken} from '../../../../helpers/token'
import * as actionType from '../../../../store/actions/action-type'

const  CreateProduct = (props) =>  {

    const [ gToken , setGToken ] = useState(GetToken());

    const [ name , setName ] = useState('');
    const [ type , setType ] = useState('');
    const [ nameWarn , setNameWarn ] = useState(false);
    const [ typeWarn , setTypeWarn ] = useState(false);
    const [ errorMsg , setErrorMsg ] = useState(false);
    const [ showLoader , setShowLoader ] = useState(false);


    const success = () => {
        Modal.success({
            title: 'Successful',
            content: 'Product has been created.',
        });
    };

    const callback = (data) => {
        if(data.status === 200){
            props.dispatchProduct(data.data);
            success();
            reset();
        }else {
            setErrorMsg('Something went wrong. Please try again.');
            setShowLoader(false);
        }
    }

    const onFormSubmit = (e) => {
        setErrorMsg('');
        setShowLoader(true);
        e.preventDefault();
        
        if(validate()){
            outlineColor();
            const data = {
                name: name,
                type: type
            };
            if(data && gToken){
                createProduct(callback, data, gToken);
            }
        }else{
            outlineColor();
            setErrorMsg('Please fill all the required field');
        }
    } 

    const onInputChange = (e) => {
        e.target.name(e.target.value);
    };

    const outlineColor = () => {
        if(!props.name.trim()){
            setNameWarn(true)
        }else{
            setNameWarn(false)
        }if(!props.type.trim()){
            setTypeWarn(true)
        }else{
            setTypeWarn(false)
        }
    }

    const validate = () => {
        return name.trim() !== '' && type.trim() !== '';
    }

    const reset = () => {
        setName('')
        setType('')
        setErrorMsg('')
        setShowLoader(false)
    }

    
          
        const columns = [{
            title: 'Product Name',
            dataIndex: 'name',
            key: 'p_name',
            }, {
            title: 'Type',
            dataIndex: 'type',
            key: 'type',
        }];

        return(
            <div className="container flex-row">
                <div className="container-left">
                    <div className="product-form">
                        <Form onSubmitHandler={onFormSubmit}>
                            <InputType 
                                type="text"
                                value={name}
                                name="name" 
                                labelName="Product Name"
                                placeholder="Enter Product Name"
                                onInputChange={onInputChange}
                                isRequired={true}
                                classValue={nameWarn ? 'inputField-outline' : null}
                            />
                            <InputType 
                                type="text"
                                value={type}
                                name="type" 
                                labelName="Product Type"
                                placeholder="Enter Product Type"
                                onInputChange={onInputChange}
                                isRequired={true}
                                classValue={typeWarn ? 'inputField-outline' : null}
                            />
                            { errorMsg ? <ErrorBox errorMsgs={errorMsg} /> : null}
                            { showLoader ? <Loader>Creating...</Loader> : null }
                            <div className="issue-form-button">
                                <Button isType='primary' htmlTypes='submit'>Create</Button>
                            </div>
                        </Form>
                    </div>
                </div>
                <div className="container-right">
                    <Table 
                        dataSource={props.products} 
                        columns={columns}
                        rowKey={record => record._id}
                        title={() => <h3><b>Products</b></h3>}
                        bordered
                        pagination={false}
                     />
                </div>
            </div>
        )
    
}

function mapDispatchToProps(dispatch) {
    return{
        dispatchProduct: (data) => {
            dispatch({
                type: actionType.ADD_PRODUCTS,
                value: data
            })
        }
    }
}

export default connect(null, mapDispatchToProps)(CreateProduct)