

import React, { Component, useState } from 'react';
import { connect } from 'react-redux'
import Form from '../../../../components/form/form';
import InputType from '../../../../components/form/form-controls/input';
import Button from '../../../../components/form/button/button';
import ErrorBox from '../../../../components/form/error-box/error-box';
import Loader from '../../../../components/ui/loader/loader';
import Dropdown from '../../../../components/form/form-controls/select';
import RichTextEditor from 'react-rte'
import ReactTextEditor from '../../../../components/form/rich-text-editor/rich-text-editor'
import { Modal } from 'antd'
import { Table, Select } from 'antd';
//
import { GetToken } from '../../../../helpers/token'
import * as actionType from '../../../../store/actions/action-type'
import { productEntry } from '../../../../services/api/product_catalog'

const Option = Select.Option;

const ProductEntry = (props) => {

    const [gToken, setgToken] = useState(GetToken())
    const [version, setversion] = useState('')
    const [productId, setproductId] = useState('')
    const [serialNumber, setserialNumber] = useState('')
    const [boardNumber, setboardNumber] = useState('')
    const [variant, setvariant] = useState(RichTextEditor.createEmptyValue())
    const [serialNumberWarn, setserialNumberWarn] = useState(false)
    const [boardNumberWarn, setboardNumberWarn] = useState(false)
    const [versionWarn, setversionWarn] = useState(false)
    const [productIdWarn, setproductIdWarn] = useState(false)
    const [errorMsg, seterrorMsg] = useState(false)
    const [showLoader, setshowLoader] = useState(false)

    const callback = (data) => {
        if (data.status === 200) {
            props.machineDispatch(data.data);
            success();
            reset();
        } else {
            seterrorMsg('Something went wrong. Please try again.')
            setshowLoader(false)
        }
    }

    const onFormSubmit = (e) => {
        seterrorMsg('')
        setshowLoader(true)
        e.preventDefault();

        const data = {
            version: version,
            serialNumber: serialNumber,
            boardNumber: boardNumber,
            variant: variant.toString('html')
        }

        if (validate() && gToken) {
            outlineColor();
            productEntry(callback, productId, data, gToken)
        } else {
            outlineColor();
            seterrorMsg('Please fill all the required field.')
            setshowLoader(false)
        }
    };

    const success = () => {
        Modal.success({
            title: 'Successful',
            content: 'Product version has been created.',
        });
    };

    const onInputChange = (e) => {
        e.target.name(e.target.value);
    };

    const onProductSelect = (value) => {
        setproductId(value)
    };

    const onVersionSelect = (value) => {
        setversion(value)
    };

    const textChange = (value) => {
        setvariant(value)
    };

    const outlineColor = () => {
        if (!productId.trim()) {
            setproductIdWarn(true)
        } else {
            setproductIdWarn(false)
        } if (!version.trim()) {
            setversionWarn(true)
        } else {
            setversionWarn(false)
        } if (!serialNumber.trim()) {
            setserialNumberWarn(true)
        } else {
            setserialNumberWarn(false)
        } if (!boardNumber.trim()) {
            setboardNumberWarn(true)
        } else {
            setboardNumberWarn(false)
        }
    }

    const validate = () => {
        return version.trim() !== '' && productId.trim() !== '' && serialNumber.trim() !== '' && boardNumber.trim() !== '';
    }

    const reset = () => {
        setserialNumber('')
        setboardNumber('')
        setshowLoader(false)
        seterrorMsg('')
        setvariant(RichTextEditor.createEmptyValue())
    }

    const columns = [{
        title: 'Versions',
        dataIndex: 'version',
        key: 'p_version',
    }, {
        title: 'Machines',
        render: (text, record) => (
            record.count
        ),
        key: 'p_count',
    }, {
        title: 'Action',
        key: 'action',
        render: (text, record) => (
            <span>
                <a href="javascript:;">View</a>
            </span>
        ),
    }];

    let filterProduct = props.products.filter((product) => product._id === productId);

    return (
        <div className="container flex-row">
            <div className="container-left">
                <div className="product-form">
                    <Form onSubmitHandler={onFormSubmit}>
                        <Dropdown
                            labelName="Product"
                            placeholder="Select Product"
                            optionFilterProp="children"
                            onSelect={onProductSelect}
                            size="large"
                            isRequired={true}
                            filterOption={(input, option) => option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0}
                            classValue={productIdWarn ? 'inputField-outline' : null}
                        >
                            {
                                props.products.map((product) => (
                                    <Option value={product._id} key={product._id}>{product.name}</Option>
                                ))
                            }
                        </Dropdown>
                        <Dropdown
                            labelName="Version"
                            placeholder="Select Version"
                            optionFilterProp="children"
                            onSelect={onVersionSelect}
                            size="large"
                            isRequired={true}
                            filterOption={(input, option) => option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0}
                            classValue={versionWarn ? 'inputField-outline' : null}
                        >
                            {
                                filterProduct[0] !== undefined && filterProduct[0] !== null && (
                                    filterProduct[0].details.map((version) => (
                                        <Option value={version.version} key={version._id}>{version.version}</Option>
                                    ))
                                )
                            }
                        </Dropdown>
                        <ErrorBox errorMsgs={<strong>If products and versions are not listed above then&nbsp;<b>Create Product/Version.</b></strong>}></ErrorBox>
                        <InputType
                            type="text"
                            value={serialNumber}
                            name="serialNumber"
                            labelName="Serial Number"
                            placeholder="Enter Product Serial Number"
                            onInputChange={onInputChange}
                            isRequired={true}
                            classValue={serialNumberWarn ? 'inputField-outline' : null}
                        />
                        <InputType
                            type="text"
                            value={boardNumber}
                            name="boardNumber"
                            labelName="Board Number"
                            placeholder="Enter Product Board Number"
                            onInputChange={onInputChange}
                            isRequired={true}
                            classValue={boardNumberWarn ? 'inputField-outline' : null}
                        />
                        <ErrorBox errorMsgs={<strong>If any variant in this machine then&nbsp;<b>Please write down.</b></strong>}></ErrorBox>
                        <ReactTextEditor
                            labelName="Variant"
                            value={variant}
                            textChange={textChange}
                            placeholder="Enter Variant of Machine"
                        />
                        {errorMsg ? <ErrorBox errorMsgs={errorMsg} /> : null}
                        {showLoader ? <Loader>Creating...</Loader> : null}
                        <div className="issue-form-button">
                            <Button isType='primary' htmlTypes='submit'>Create</Button>
                        </div>
                    </Form>
                </div>
            </div>
            <div className="container-right">
                {
                    props.products.map((product) => (
                        <div key={product._id} className="versionTable">
                            <Table
                                dataSource={product.details}
                                columns={columns}
                                rowKey={record => record._id}
                                title={() => <h3><b>{product.name}</b>&nbsp;&nbsp;<b style={{ color: 'green' }}>avl.({product.count})</b></h3>}
                                bordered
                                pagination={false}
                            />
                        </div>
                    ))
                }
            </div>
        </div>
    )

}

function mapDispatchToProps(dispatch) {
    return {
        machineDispatch: (data) => {
            dispatch({
                type: actionType.ADD_MACHINE,
                value: data
            })
        }
    }
}

export default connect(null, mapDispatchToProps)(ProductEntry)