import React, { Component, useEffect, useState } from 'react'
import { connect } from 'react-redux'
import { Tabs } from 'antd';
import Icon from '@ant-design/icons';
import CreateProduct from './create_product/create_product';
import Dashboard from './dashboard/dashboard';
import CreateVersion from './create_version/create_version';
import MachineEntry from './product_entry/product_entry';
import './product_catalogue.css'
import Loader from '../../../components/ui/loader/loader'
import * as actionType from '../../../store/actions/action-type'
import { getAllProduct } from '../../../services/api/product_catalog';
import { GetToken } from '../../../helpers/token'

const TabPane = Tabs.TabPane;

const ProductCatalogue = (props) => {

    const [gToken, setGToken] = useState(GetToken());

    const [showLoader, setShowLoader] = useState(props.firstRunProducts);

    const callback = (data) => {
        if (data.status === 200) {
            props.getProducts(data.data);
            setShowLoader({ showLoader: false });
            props.dispatchUpdateFirstRunProducts(false);
        } else {
            console.log(data.response)
        }
    }

    useEffect(() => {
        if (props.firstRunProducts) {
            getAllProduct(callback, gToken);
        }
    },[])


    if (showLoader && !props.products) {
        return (
            <Loader />
        )
    } else {
        return (
            <Tabs defaultActiveKey="1" >
                <TabPane tab={<span><Icon type="dashboard" />Dashboard</span>} key="1" >
                    <Dashboard products={props.products} />
                </TabPane>
                <TabPane tab={<span><Icon type="form" />Machine Entry</span>} key="2">
                    <MachineEntry products={props.products} />
                </TabPane>
                <TabPane tab={<span><Icon type="copy" />Create Version</span>} key="3">
                    <CreateVersion products={props.products} />
                </TabPane>
                <TabPane tab={<span><Icon type="setting" />Create Product</span>} key="4">
                    <CreateProduct products={props.products} />
                </TabPane>
            </Tabs>
        )
    }



}

function mapStateToProps(state) {
    return {
        products: state.Products.products,
        firstRunProducts: state.Products.firstRun
    }
}

function mapDispatchToProps(dispatch) {
    return {
        getProducts: (data) => {
            dispatch({
                type: actionType.PRODUCTS,
                value: data
            })
        },
        dispatchUpdateFirstRunProducts: (data) => {
            dispatch({
                type: actionType.UPDATE_FIRST_RUN_PRODUCTS,
                value: data
            })
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(ProductCatalogue)